<template>
  <van-tabbar v-model="active" v-on:change="change" fixed="true">
    <van-tabbar-item icon="home-o">首頁</van-tabbar-item>
    <van-tabbar-item icon ="tv-o">電視劇</van-tabbar-item>
    <van-tabbar-item icon="video-o">綜藝</van-tabbar-item>
    <van-tabbar-item icon="fire-o">動漫</van-tabbar-item>
  </van-tabbar>
</template>
<script setup>
    import {ref, onMounted} from "vue"
    import {useRouter, useRoute} from "vue-router"
    const router = useRouter()
    const route = useRoute()
    const active = ref(0)
    const change = (value)=>{
        switch(value){
            case 0:
            router.push({name:"Home"})
            break
            case 1:
            router.push({name:"Drama"})
            break
            case 2:
            router.push({name:"Variety"})
            break
            case 3:
            router.push({name:"Cartoon"})
            break
        }
    }
    onMounted(()=>{ //組件加載後
        if(route.name==="Home"){
            active.value=0
        }else if(route.name==="Drama"){
            active.value=1
        }else if(route.name==="Variety"){
            active.value=2
        }else if(route.name==="Cartoon"){
            active.value=3
        }
    })
</script>