<template>
         <van-nav-bar  title="BBIbbi" fixed placeholder :border="false"/>
         <div class="search-box">
            <van-button @click="goSearch">
                <van-icon name="search" />搜索
            </van-button>
    </div> 
    <div class="container-swiper">
         <van-swipe class="my-swipe" :autoplay="2000" indicator-color="white">
            <van-swipe-item><a href="http://localhost:8080/#/filminfo?mId=12"><img src="../../assets/images/2.png" alt=""></a></van-swipe-item>
            <van-swipe-item><a href="http://localhost:8080/#/filminfo?mId=8"><img src="../../assets/images/3.png" alt=""></a></van-swipe-item>
            <van-swipe-item><a href="http://localhost:8080/#/filminfo?mId=11"><img src="../../assets/images/4.jpg" alt=""></a></van-swipe-item>
            <van-swipe-item><a href="http://localhost:8080/#/filminfo?mId=25"><img src="../../assets/images/5.jpg" alt=""></a></van-swipe-item>
        </van-swipe>
    </div>
    <div class="reflection" v-for="item in state.homeList" :key="item.tId">
            <van-cell :border="false" :title="item.tName" />
            <div class="list">
                <ul>
                    <li v-for="items in item[0]" :key="items.mId"
                    @click="goFilmInfo(items.mId)">
                        <div class="img">
                            <van-image width="90" height="120" 
                            :src="`http://localhost:9999/${items.mImg}`" />
                        </div>
                        <div class="title">{{items.mName}}</div>
                        <div class="rate">
                            <van-rate color="#ffac2d" v-model="items.cmScore" readonly allow-half />
                        <div class="text">{{ items.cmScore }}</div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>    
      
    <TabBar></TabBar>
</template>
<script setup>
import TabBar from "@/components/TabBar/TabBar.vue"
import {ref,reactive,onMounted} from "vue"
import { getHomeList } from "@/api/home"
import { useRouter } from "vue-router"

const router = useRouter()

const state = reactive({
    homeList:[]
})
onMounted(async ()=>{
    const { data } = await getHomeList();
    data.forEach(item=>{
        item[0].forEach(items=>{
            items.cmScore = parseFloat(items.mScore/2);
        })
    })
    state.homeList = data
})
const goFilmInfo = (value)=>{
  router.push({name:"FilmInfo",query:{mId:value}})
}

const goSearch = ()=>{
  router.push("/Search")
}



</script>
<style lang="scss" scoped>
.my-swipe .van-swipe-item {
    width: 100%;
    height: 230px;
    color: #fff;
    font-size: 20px;
    text-align: center;
    background-color: #e2e7eb;
  }

.container-swiper {
  margin: 0.5rem 0.5rem;
}
.container-swiper img {
    display: inline-block;
    width: 100%;
    height: 228px;
}

.search-box {
  padding: 2.667vw;
  background-color: #fff;
}

.search-box button {
  width: 100%;
  height: 8vw;
  border-radius: 1.333vw;
  color: #999;
}
    
.reflection {
    .list {
    margin-bottom: 2.667vw;
    padding: 0 4.267vw;
    ul {
      overflow: auto;
      display: flex;
      & li:nth-child(1) {
        margin-left: 0;
      }
      &::-webkit-scrollbar {
        display: none;
      }
      li {
        margin-left: 4.267vw;
        position: relative;
        .img {
          :deep(van-image__img) {
            border-radius: 1.333vw;
          }
        }
        .title {
          width: 24vw;
          font-size: 3.733vw;
          font-weight: 700;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .rate {
          .van-rate {
            width: 37.333vw;
            position: absolute;
            transform: scale(0.5);
            bottom: -0.533vw;
            left: -9.6vw;
          }
          .text {
            font-size: 3.733vw;
            margin-left: 16.533vw;
          }
        }
      }
    }
  }
}

</style>